
.rjcv-actionsheet .rjcv-actionsheet-title {
  padding: 0.2rem;
  color: #666;
  background-color: #fff;
}
.rjcv-actionsheet .rjcv-actionsheet-btn, .rjcv-actionsheet .rjcv-actionsheet-action {
  width: 100%;
  color: #121212;
  background-color: #fff;
  border-radius: 0;
}
.rjcv-actionsheet .rjcv-actionsheet-btn {
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(50%, #ddd), color-stop(50%, transparent));
  background-image: -webkit-linear-gradient(top, #ddd 50%, transparent 50%);
  background-image: -o-linear-gradient(top, #ddd 50%, transparent 50%);
  background-image: linear-gradient(to bottom, #ddd 50%, transparent 50%);
  background-position: left top;
  background-size: 100% 1px;
  background-repeat: no-repeat;
}
.rjcv-actionsheet .rjcv-actionsheet-action {
  margin-top: 0.2rem;
  color: #999;
}

.rjcv-button {
  padding: 0.2rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 0.32rem;
  line-height: 1.5;
  color: #fff;
  word-break: break-all;
  background-color: #ff397e;
  border: 0;
  border-radius: 0;
  outline: 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.rjcv-popup {
  font-size: 0.3rem;
  line-height: 1.5;
  word-break: break-all;
}
.rjcv-popup .rjcv-popup-top {
    position: fixed;
    top: 0;
    left: 50%;
    z-index: 2001;
    width: 7.5rem;
    color: #fff;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.7);
    -webkit-transform: translate3d(-50%, 0, 0);
            transform: translate3d(-50%, 0, 0);
}
.rjcv-popup .rjcv-popup-bottom {
    position: fixed;
    bottom: 0;
    left: 50%;
    z-index: 2001;
    width: 7.5rem;
    color: #fff;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.7);
    -webkit-transform: translate3d(-50%, 0, 0);
            transform: translate3d(-50%, 0, 0);
}
.rjcv-popup .rjcv-popup-right, .rjcv-popup .rjcv-popup-left {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 50%;
    z-index: 2001;
    width: 7.5rem;
    background-color: #fff;
    -webkit-transform: translate3d(-50%, 0, 0);
            transform: translate3d(-50%, 0, 0);
}
.rjcv-popup .rjcv-popup-core {
    position: fixed;
    top: 50%;
    left: 50%;
    z-index: 2001;
    color: #fff;
    -webkit-transform: translate3d(-50%, -50%, 0);
            transform: translate3d(-50%, -50%, 0);
}
.rjcv-popup .rjcv-popup-core.rjcv-popup-nomask {
      color: #121212;
}
.rjcv-fade-enter-active, .rjcv-fade-leave-active {
  -webkit-transition: opacity .3s ease;
  -o-transition: opacity .3s ease;
  transition: opacity .3s ease;
}
.rjcv-fade-enter, .rjcv-fade-leave-to {
  opacity: 0 !important;
}
.rjcv-top-enter-active, .rjcv-top-leave-active, .rjcv-right-enter-active, .rjcv-right-leave-active, .rjcv-bottom-enter-active, .rjcv-bottom-leave-active, .rjcv-left-enter-active, .rjcv-left-leave-active {
  -webkit-transition: -webkit-transform .3s ease;
  transition: -webkit-transform .3s ease;
  -o-transition: transform .3s ease;
  transition: transform .3s ease;
  transition: transform .3s ease, -webkit-transform .3s ease;
}
.rjcv-top-enter, .rjcv-top-leave-to {
  -webkit-transform: translate3d(-50%, -100%, 0) !important;
          transform: translate3d(-50%, -100%, 0) !important;
}
.rjcv-right-enter, .rjcv-right-leave-to {
  -webkit-transform: translate3d(50%, 0, 0) !important;
          transform: translate3d(50%, 0, 0) !important;
}
.rjcv-bottom-enter, .rjcv-bottom-leave-to {
  -webkit-transform: translate3d(-50%, 100%, 0) !important;
          transform: translate3d(-50%, 100%, 0) !important;
}
.rjcv-left-enter, .rjcv-left-leave-to {
  -webkit-transform: translate3d(-150%, 0, 0) !important;
          transform: translate3d(-150%, 0, 0) !important;
}

.rjcv-mask {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 50%;
  z-index: 2000;
  width: 7.5rem;
  font-size: 0.3rem;
  line-height: 1.5;
  color: #fff;
  word-break: break-all;
  background-color: rgba(0, 0, 0, 0.3);
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
}
